@import "../../inc/vars";
.menu-cta-block {
	padding: 18px 0 0 16px;
	position: relative;
	border-radius: 12px;
	box-shadow: 0px 9px 33px 0px rgba(162, 165, 168, 0.20), 0px 1.127px 4.132px 0px rgba(162, 165, 168, 0.41);
	margin-bottom: 10px;
	@media(min-width: $screen-sm){
		padding: 40px 0 0 36px;
		box-shadow: 0px 9px 33px 0px rgba(162, 165, 168, 0.20), 0px 1.127px 4.132px 0px rgba(162, 165, 168, 0.41);
		margin-bottom: 28px;
	}
	@media(min-width: 1310px){
		border-radius: 0;
		padding: 24px 72px 12px 24px;
		box-shadow: none;
		margin-bottom: 0;
	}
	&:before {
		content: '';
		position: absolute;
		width: 1px;
		height: 100%;
		background: #D0CDD5;
		right: 24px;
		top: 0;
		display: none;
		@media (min-width: 1310px){
			display: block;
		}
	}
	&:after {
		content: '';
		position: absolute;
		width: calc(100% - 49px);
		height: 100%;
		background: #F6F7F7;
		box-shadow: 0px 100px 80px 0px rgba(0, 0, 0, 0.01), 0px 41.778px 33.422px 0px rgba(0, 0, 0, 0.01), 0px 22.336px 17.869px 0px rgba(0, 0, 0, 0.01), 0px 12.522px 10.017px 0px rgba(0, 0, 0, 0.01), 0px 6.65px 5.32px 0px rgba(0, 0, 0, 0.01), 0px 2.767px 2.214px 0px rgba(0, 0, 0, 0.02);
		right: 49px;
		top: 0;
		opacity: 0;
		transition: 0.3s;
		border-radius: 6px;
		display: block;
	}
	&:hover {
		&:after {
			opacity: 1;
		}
	}
	a {
		z-index: 2;
		font-size: 22px !important;
		font-weight: 400 !important;
		line-height: 124% !important;
		letter-spacing: -0.58px !important;
		color: #4F4B58 !important;
		font-family: 'Poppins', sans-serif !important;
		display: flex !important;
		width: 100%;
		justify-content: space-between;
		padding: 0 !important;
		text-decoration: none !important;
		
		&:hover {
			color: $purple !important;
		}
		&:first-child {
			margin-bottom: 24px;
			position: absolute;
			top: 18px;
			left: 16px;
			@media (min-width: $screen-sm){
				font-size: 36px !important;
				top: 40px;
				left: 36px;
			}
			@media (min-width: 1310px){
				font-size: 29px !important;
				position: relative;
				left: 0;
				top: 0;
			}
			img {
				display: none;
				@media (min-width: 1310px){
					display: block;
				}
			}
		}
		&:last-child {
			text-align: right;
		}
	}
	img {
		position: relative;
		z-index: 2;
	}
	&__image {
		transition: 0.4s;
		margin-left: auto;
		width: 100%;
		height: auto;
		opacity: 0.8;
		@media (min-width: $screen-sm){
			opacity: 1;
		}
	}
	&:hover {
		.menu-cta-block__image {
			transform: scale(1.1);
		}
	}
}